"use client"

import { useCart } from '@/contexts/CartContext'
import { Button } from '@/components/ui/button'
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetTrigger } from '@/components/ui/sheet'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import { ScrollArea } from '@/components/ui/scroll-area'
import CartItem from './CartItem'
import CartSummary from './CartSummary'
import { motion, AnimatePresence } from 'framer-motion'

interface CartDrawerProps {
  children?: React.ReactNode
}

export default function CartDrawer({ children }: CartDrawerProps) {
  const { state, closeCart } = useCart()

  return (
    <Sheet open={state.isOpen} onOpenChange={(open) => !open && closeCart()}>
      {children && <SheetTrigger asChild>{children}</SheetTrigger>}
      
      <SheetContent className="w-full sm:max-w-lg flex flex-col">
        <SheetHeader className="space-y-4">
          <div className="flex items-center justify-between">
            <SheetTitle className="font-playfair text-2xl font-light text-neutral-800">
              Your Cart
            </SheetTitle>
            <motion.div
              key={state.itemCount}
              initial={{ scale: 1.2 }}
              animate={{ scale: 1 }}
              transition={{ duration: 0.2 }}
            >
              <Badge 
                variant="secondary" 
                className="bg-amber-100 text-amber-800 px-3 py-1"
              >
                {state.itemCount} {state.itemCount === 1 ? 'item' : 'items'}
              </Badge>
            </motion.div>
          </div>
          <Separator />
        </SheetHeader>

        {state.items.length === 0 ? (
          <div className="flex-1 flex flex-col items-center justify-center text-center py-12">
            <div className="w-24 h-24 bg-neutral-100 rounded-full flex items-center justify-center mb-6">
              <div className="w-12 h-12 border-2 border-neutral-300 rounded-full flex items-center justify-center">
                <span className="text-neutral-400 text-xl">üõí</span>
              </div>
            </div>
            <h3 className="font-playfair text-xl font-light text-neutral-700 mb-2">
              Your cart is empty
            </h3>
            <p className="text-neutral-500 text-sm mb-6 max-w-sm">
              Discover our healing services, courses, and transformational programs to begin your journey.
            </p>
            <Button 
              onClick={closeCart}
              variant="outline"
              className="border-amber-200 text-amber-700 hover:bg-amber-50"
            >
              Continue Browsing
            </Button>
          </div>
        ) : (
          <>
            {/* Cart Items */}
            <ScrollArea className="flex-1 -mx-6 px-6">
              <div className="space-y-4 py-4">
                <AnimatePresence mode="popLayout">
                  {state.items.map((item, index) => (
                    <motion.div
                      key={item.id}
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      exit={{ opacity: 0, x: -100 }}
                      transition={{ duration: 0.2, delay: index * 0.05 }}
                      layout
                    >
                      <CartItem item={item} />
                    </motion.div>
                  ))}
                </AnimatePresence>
              </div>
            </ScrollArea>

            <Separator className="my-4" />

            {/* Cart Summary */}
            <div className="space-y-4">
              <CartSummary />
            </div>
          </>
        )}
      </SheetContent>
    </Sheet>
  )
}

// Cart Icon Button Component
export function CartIconButton() {
  const { state, toggleCart } = useCart()

  return (
    <motion.div
      whileHover={{ scale: 1.05 }}
      whileTap={{ scale: 0.95 }}
      className="relative"
    >
      <Button
        onClick={toggleCart}
        variant="outline"
        size="sm"
        className="relative border-neutral-200 hover:bg-neutral-50 p-3"
      >
        <span className="text-lg">üõí</span>
        
        <AnimatePresence>
          {state.itemCount > 0 && (
            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              exit={{ scale: 0 }}
              className="absolute -top-2 -right-2"
            >
              <Badge 
                variant="default" 
                className="bg-amber-600 hover:bg-amber-700 text-white text-xs px-2 py-1 min-w-[20px] h-5 flex items-center justify-center"
              >
                {state.itemCount > 99 ? '99+' : state.itemCount}
              </Badge>
            </motion.div>
          )}
        </AnimatePresence>
      </Button>
    </motion.div>
  )
}

// Cart Summary Badge (for showing total in navigation)
export function CartSummaryBadge() {
  const { state, toggleCart } = useCart()

  if (state.itemCount === 0) return null

  return (
    <motion.div
      initial={{ opacity: 0, y: -10 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -10 }}
      className="cursor-pointer"
      onClick={toggleCart}
    >
      <Badge 
        variant="secondary" 
        className="bg-amber-100 text-amber-800 hover:bg-amber-200 transition-colors px-3 py-1"
      >
        {state.itemCount} items ‚Ä¢ ${state.total.toFixed(2)}
      </Badge>
    </motion.div>
  )
}
