"use client"

import { useCart } from '@/contexts/CartContext'
import { Button } from '@/components/ui/button'
import { Separator } from '@/components/ui/separator'
import { Badge } from '@/components/ui/badge'
import { motion } from 'framer-motion'
import { useState } from 'react'
import { getStripe } from '@/lib/stripe'

export default function CartSummary() {
  const { state, clearCart, closeCart } = useCart()
  const [isProcessing, setIsProcessing] = useState(false)
  const [promoCode, setPromoCode] = useState('')
  const [discount, setDiscount] = useState(0)

  // Calculate totals
  const subtotal = state.total
  const discountAmount = subtotal * (discount / 100)
  const total = subtotal - discountAmount

  const handleCheckout = async () => {
    setIsProcessing(true)
    
    try {
      // Create checkout session
      const response = await fetch('/api/create-checkout-session', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          items: state.items,
          successUrl: `${window.location.origin}/checkout/success`,
          cancelUrl: `${window.location.origin}/checkout/cancel`,
          promoCode: promoCode || undefined,
        }),
      })

      if (!response.ok) {
        throw new Error('Failed to create checkout session')
      }

      const { sessionId } = await response.json()
      
      // Redirect to Stripe Checkout
      const stripe = await getStripe()
      if (stripe) {
        const { error } = await stripe.redirectToCheckout({ sessionId })
        if (error) {
          throw error
        }
      }
    } catch (error) {
      console.error('Checkout error:', error)
      
      // For demo purposes, show alert with cart contents
      const itemsList = state.items.map(item => 
        `‚Ä¢ ${item.name} (${item.quantity}x) - $${(item.price * item.quantity).toFixed(2)}`
      ).join('\n')
      
      alert(`Checkout Demo\n\nItems:\n${itemsList}\n\nTotal: $${total.toFixed(2)}\n\nNext steps:\n1. Set up Stripe API routes\n2. Configure webhook endpoints\n3. Add success/cancel pages`)
    } finally {
      setIsProcessing(false)
    }
  }

  const handleClearCart = () => {
    if (confirm('Are you sure you want to clear your cart?')) {
      clearCart()
    }
  }

  const [promoError, setPromoError] = useState('')

  const applyPromoCode = () => {
    setPromoError('')
    
    if (!promoCode.trim()) {
      setPromoError('Please enter a promo code')
      return
    }

    // Demo promo codes
    const promoCodes: Record<string, number> = {
      'WELCOME10': 10,
      'HEALING20': 20,
      'FIRSTTIME': 15,
      'STUDENT': 25
    }

    const discountPercent = promoCodes[promoCode.toUpperCase()]
    if (discountPercent) {
      setDiscount(discountPercent)
      setPromoError('')
    } else {
      setPromoError('Invalid promo code. Try WELCOME10, HEALING20, FIRSTTIME, or STUDENT')
      setPromoCode('')
    }
  }

  const removePromoCode = () => {
    setDiscount(0)
    setPromoCode('')
  }

  // Group items by type for better summary
  const itemsByType = state.items.reduce((acc, item) => {
    if (!acc[item.type]) {
      acc[item.type] = []
    }
    acc[item.type].push(item)
    return acc
  }, {} as Record<string, typeof state.items>)

  return (
    <div className="space-y-4">
      {/* Order Summary */}
      <div className="space-y-3">
        <h4 className="font-medium text-neutral-900">Order Summary</h4>
        
        {/* Items by Type */}
        <div className="space-y-2 text-sm">
          {Object.entries(itemsByType).map(([type, items]) => (
            <div key={type} className="flex justify-between items-center">
              <span className="text-neutral-600 capitalize">
                {type}s ({items.length})
              </span>
              <span className="font-medium">
                ${items.reduce((sum, item) => sum + (item.price * item.quantity), 0).toFixed(2)}
              </span>
            </div>
          ))}
        </div>

        <Separator />

        {/* Promo Code */}
        <div className="space-y-2">
          {discount > 0 ? (
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <Badge variant="secondary" className="bg-green-100 text-green-800">
                  {promoCode.toUpperCase()} (-{discount}%)
                </Badge>
                <Button
                  onClick={removePromoCode}
                  variant="ghost"
                  size="sm"
                  className="text-xs text-neutral-500 hover:text-red-600 p-0 h-auto"
                >
                  Remove
                </Button>
              </div>
              <span className="text-green-600 font-medium">
                -${discountAmount.toFixed(2)}
              </span>
            </div>
          ) : (
            <div className="space-y-2">
              <div className="flex gap-2">
                <input
                  type="text"
                  placeholder="Promo code"
                  value={promoCode}
                  onChange={(e) => {
                    setPromoCode(e.target.value)
                    setPromoError('')
                  }}
                  className={`flex-1 px-3 py-2 text-sm border rounded-md focus:outline-none focus:ring-2 focus:ring-amber-500 focus:border-transparent ${
                    promoError ? 'border-red-300 bg-red-50' : 'border-neutral-300'
                  }`}
                />
                <Button
                  onClick={applyPromoCode}
                  variant="outline"
                  size="sm"
                  disabled={!promoCode.trim()}
                  className="px-4"
                >
                  Apply
                </Button>
              </div>
              {promoError && (
                <p className="text-xs text-red-600 mt-1">{promoError}</p>
              )}
            </div>
          )}
        </div>

        <Separator />

        {/* Totals */}
        <div className="space-y-2">
          <div className="flex justify-between text-sm">
            <span className="text-neutral-600">Subtotal</span>
            <span>${subtotal.toFixed(2)}</span>
          </div>
          
          {discount > 0 && (
            <div className="flex justify-between text-sm text-green-600">
              <span>Discount ({discount}%)</span>
              <span>-${discountAmount.toFixed(2)}</span>
            </div>
          )}
          
          <Separator />
          
          <div className="flex justify-between font-medium text-lg">
            <span>Total</span>
            <motion.span
              key={total}
              initial={{ scale: 1.1 }}
              animate={{ scale: 1 }}
              className="text-amber-700"
            >
              ${total.toFixed(2)}
            </motion.span>
          </div>
        </div>
      </div>

      {/* Action Buttons */}
      <div className="space-y-3 pt-2">
        <motion.div
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
        >
          <Button
            onClick={handleCheckout}
            disabled={isProcessing || state.items.length === 0}
            className="w-full bg-gradient-to-r from-amber-600 to-amber-700 hover:from-amber-700 hover:to-amber-800 text-white font-medium py-3 rounded-lg shadow-lg hover:shadow-xl transition-all duration-300"
          >
            {isProcessing ? (
              <div className="flex items-center gap-2">
                <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                Processing...
              </div>
            ) : (
              `Proceed to Checkout ‚Ä¢ $${total.toFixed(2)}`
            )}
          </Button>
        </motion.div>

        <div className="flex gap-2">
          <Button
            onClick={closeCart}
            variant="outline"
            className="flex-1 border-neutral-300 text-neutral-700 hover:bg-neutral-50"
          >
            Continue Shopping
          </Button>
          
          <Button
            onClick={handleClearCart}
            variant="ghost"
            className="text-neutral-500 hover:text-red-600 hover:bg-red-50"
          >
            Clear Cart
          </Button>
        </div>
      </div>

      {/* Trust Indicators */}
      <div className="pt-4 border-t border-neutral-200">
        <div className="flex items-center justify-center gap-4 text-xs text-neutral-500">
          <div className="flex items-center gap-1">
            <span>üîí</span>
            <span>Secure Checkout</span>
          </div>
          <div className="flex items-center gap-1">
            <span>üí≥</span>
            <span>Stripe Protected</span>
          </div>
          <div className="flex items-center gap-1">
            <span>‚Ü©Ô∏è</span>
            <span>Easy Refunds</span>
          </div>
        </div>
      </div>
    </div>
  )
}
