"use client"

import { CartItem as CartItemType, useCart } from '@/contexts/CartContext'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import { motion } from 'framer-motion'
import { useState } from 'react'

interface CartItemProps {
  item: CartItemType
}

export default function CartItem({ item }: CartItemProps) {
  const { removeItem, updateQuantity } = useCart()
  const [isRemoving, setIsRemoving] = useState(false)

  const handleRemove = async () => {
    setIsRemoving(true)
    // Add a small delay for better UX
    await new Promise(resolve => setTimeout(resolve, 200))
    removeItem(item.id)
  }

  const handleQuantityChange = (newQuantity: number) => {
    if (newQuantity <= 0) {
      handleRemove()
    } else {
      updateQuantity(item.id, newQuantity)
    }
  }

  const getItemTypeLabel = (type: string) => {
    switch (type) {
      case 'service': return 'Session'
      case 'course': return 'Course'
      case 'workshop': return 'Workshop'
      case 'retreat': return 'Retreat'
      case 'product': return 'Product'
      case 'membership': return 'Membership'
      default: return 'Item'
    }
  }

  const getItemTypeColor = (type: string) => {
    switch (type) {
      case 'service': return 'bg-blue-100 text-blue-800'
      case 'course': return 'bg-purple-100 text-purple-800'
      case 'workshop': return 'bg-green-100 text-green-800'
      case 'retreat': return 'bg-orange-100 text-orange-800'
      case 'product': return 'bg-gray-100 text-gray-800'
      case 'membership': return 'bg-amber-100 text-amber-800'
      default: return 'bg-neutral-100 text-neutral-800'
    }
  }

  const canChangeQuantity = !['service', 'workshop', 'retreat'].includes(item.type)

  return (
    <motion.div
      layout
      initial={{ opacity: 1 }}
      animate={{ 
        opacity: isRemoving ? 0.5 : 1,
        scale: isRemoving ? 0.95 : 1
      }}
      className="bg-white border border-neutral-200 rounded-lg p-4 space-y-3"
    >
      {/* Item Header */}
      <div className="flex items-start justify-between">
        <div className="flex-1 min-w-0">
          <div className="flex items-center gap-2 mb-2">
            <Badge 
              variant="secondary" 
              className={`text-xs px-2 py-1 ${getItemTypeColor(item.type)}`}
            >
              {getItemTypeLabel(item.type)}
            </Badge>
            {item.duration && (
              <span className="text-xs text-neutral-500">
                {item.duration}
              </span>
            )}
          </div>
          
          <h4 className="font-medium text-neutral-900 text-sm leading-tight mb-1">
            {item.name}
          </h4>
          
          {item.description && (
            <p className="text-xs text-neutral-600 line-clamp-2">
              {item.description}
            </p>
          )}

          {/* Additional Info */}
          {(item.date || item.location) && (
            <div className="flex flex-wrap gap-2 mt-2 text-xs text-neutral-500">
              {item.date && (
                <span className="flex items-center gap-1">
                  üìÖ {item.date}
                </span>
              )}
              {item.location && (
                <span className="flex items-center gap-1">
                  üìç {item.location}
                </span>
              )}
            </div>
          )}
        </div>

        {/* Remove Button */}
        <Button
          onClick={handleRemove}
          variant="ghost"
          size="sm"
          disabled={isRemoving}
          className="text-neutral-400 hover:text-red-600 hover:bg-red-50 p-1 h-auto min-w-0"
        >
          <span className="text-lg">√ó</span>
        </Button>
      </div>

      <Separator />

      {/* Price and Quantity */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          {/* Quantity Controls */}
          {canChangeQuantity ? (
            <div className="flex items-center gap-2">
              <Button
                onClick={() => handleQuantityChange(item.quantity - 1)}
                variant="outline"
                size="sm"
                disabled={isRemoving}
                className="h-8 w-8 p-0 border-neutral-300"
              >
                -
              </Button>
              
              <span className="text-sm font-medium min-w-[20px] text-center">
                {item.quantity}
              </span>
              
              <Button
                onClick={() => handleQuantityChange(item.quantity + 1)}
                variant="outline"
                size="sm"
                disabled={isRemoving}
                className="h-8 w-8 p-0 border-neutral-300"
              >
                +
              </Button>
            </div>
          ) : (
            <div className="flex items-center gap-2">
              <span className="text-sm text-neutral-600">Qty:</span>
              <span className="text-sm font-medium">{item.quantity}</span>
            </div>
          )}
        </div>

        {/* Price */}
        <div className="text-right">
          <div className="text-sm font-medium text-neutral-900">
            ${(item.price * item.quantity).toFixed(2)}
          </div>
          {item.quantity > 1 && (
            <div className="text-xs text-neutral-500">
              ${item.price.toFixed(2)} each
            </div>
          )}
          {item.type === 'membership' && (
            <div className="text-xs text-neutral-500">
              per month
            </div>
          )}
        </div>
      </div>
    </motion.div>
  )
}
