"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { useCart, CartItem } from '@/contexts/CartContext'
import { motion } from 'framer-motion'
import { Badge } from '@/components/ui/badge'

interface AddToCartButtonProps {
  item: Omit<CartItem, 'quantity'>
  className?: string
  variant?: 'default' | 'outline' | 'secondary' | 'ghost'
  size?: 'sm' | 'default' | 'lg'
  showPrice?: boolean
  disabled?: boolean
}

export default function AddToCartButton({
  item,
  className = "",
  variant = "default",
  size = "default",
  showPrice = true,
  disabled = false
}: AddToCartButtonProps) {
  const { addItem, openCart, state } = useCart()
  const [isAdding, setIsAdding] = useState(false)
  const [justAdded, setJustAdded] = useState(false)

  // Check if item is already in cart
  const isInCart = state.items.some(cartItem => cartItem.id === item.id)
  const cartItem = state.items.find(cartItem => cartItem.id === item.id)

  const handleAddToCart = async () => {
    if (disabled) return

    setIsAdding(true)
    
    // Add item to cart
    addItem(item)
    
    // Show feedback animation
    setJustAdded(true)
    
    // Simulate loading for better UX
    await new Promise(resolve => setTimeout(resolve, 500))
    
    setIsAdding(false)
    
    // Reset feedback after animation
    setTimeout(() => {
      setJustAdded(false)
    }, 2000)

    // For services and single-purchase items, open cart immediately
    if (item.type === 'service' || item.type === 'workshop') {
      setTimeout(() => {
        openCart()
      }, 600)
    }
  }

  const getButtonText = () => {
    if (isAdding) return 'Adding...'
    if (justAdded) return 'Added!'
    if (isInCart && (item.type === 'service' || item.type === 'workshop')) {
      return 'In Cart'
    }
    if (isInCart && cartItem) {
      return `Add Another (${cartItem.quantity} in cart)`
    }
    
    switch (item.type) {
      case 'service':
        return 'Book Session'
      case 'course':
        return showPrice ? `Enroll - $${item.price}` : 'Enroll Now'
      case 'workshop':
        return showPrice ? `Register - $${item.price}` : 'Register'
      case 'retreat':
        return showPrice ? `Reserve Spot - $${item.price}` : 'Reserve Spot'
      case 'product':
        return showPrice ? `Add to Cart - $${item.price}` : 'Add to Cart'
      case 'membership':
        return showPrice ? `Join - $${item.price}/mo` : 'Join Now'
      default:
        return showPrice ? `Add to Cart - $${item.price}` : 'Add to Cart'
    }
  }

  const getButtonVariant = () => {
    if (justAdded) return 'default'
    if (isInCart && (item.type === 'service' || item.type === 'workshop')) {
      return 'outline'
    }
    return variant
  }

  return (
    <div className="relative">
      <motion.div
        whileHover={{ scale: disabled ? 1 : 1.02 }}
        whileTap={{ scale: disabled ? 1 : 0.98 }}
        animate={justAdded ? { scale: [1, 1.05, 1] } : {}}
        transition={{ duration: 0.2 }}
      >
        <Button
          onClick={handleAddToCart}
          disabled={disabled || isAdding}
          variant={getButtonVariant()}
          size={size}
          className={`
            relative overflow-hidden transition-all duration-300
            ${justAdded ? 'bg-green-600 hover:bg-green-700 text-white' : ''}
            ${isInCart && (item.type === 'service' || item.type === 'workshop') ? 'border-green-500 text-green-700' : ''}
            ${className}
          `}
        >
          <motion.span
            initial={{ opacity: 1 }}
            animate={{ opacity: isAdding ? 0.7 : 1 }}
            className="flex items-center gap-2"
          >
            {getButtonText()}
            {justAdded && (
              <motion.span
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                className="text-sm"
              >
                ‚úì
              </motion.span>
            )}
          </motion.span>

          {/* Loading animation */}
          {isAdding && (
            <motion.div
              className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent"
              initial={{ x: '-100%' }}
              animate={{ x: '100%' }}
              transition={{ duration: 1, repeat: Infinity }}
            />
          )}
        </Button>
      </motion.div>

      {/* Quantity badge for items in cart */}
      {isInCart && cartItem && cartItem.quantity > 1 && (
        <motion.div
          initial={{ scale: 0 }}
          animate={{ scale: 1 }}
          className="absolute -top-2 -right-2"
        >
          <Badge variant="secondary" className="bg-amber-100 text-amber-800 text-xs px-2 py-1">
            {cartItem.quantity}
          </Badge>
        </motion.div>
      )}
    </div>
  )
}

// Specialized components for different item types
export function BookSessionButton({ item, ...props }: Omit<AddToCartButtonProps, 'item'> & { item: Omit<CartItem, 'quantity'> }) {
  return (
    <AddToCartButton
      item={{ ...item, type: 'service' }}
      className="bg-gradient-to-r from-amber-600 to-amber-700 hover:from-amber-700 hover:to-amber-800 text-white font-light tracking-wide rounded-full px-6 py-3 shadow-lg hover:shadow-xl transition-all duration-300 border-0"
      {...props}
    />
  )
}

