"use client"

import { useEffect } from 'react'
import { Button } from '@/components/ui/button'

interface CalendlyWidgetProps {
  url?: string
  prefill?: {
    name?: string
    email?: string
    customAnswers?: Record<string, string>
  }
  utm?: {
    utmCampaign?: string
    utmSource?: string
    utmMedium?: string
    utmContent?: string
    utmTerm?: string
  }
}

declare global {
  interface Window {
    Calendly: any
  }
}

export default function CalendlyWidget({ 
  url = process.env.NEXT_PUBLIC_CALENDLY_URL || 'https://calendly.com/revika-healing',
  prefill = {},
  utm = {}
}: CalendlyWidgetProps) {
  
  useEffect(() => {
    // Load Calendly script if not already loaded
    if (!window.Calendly) {
      const script = document.createElement('script')
      script.src = 'https://assets.calendly.com/assets/external/widget.js'
      script.async = true
      document.head.appendChild(script)
    }
  }, [])

  const openCalendlyPopup = () => {
    if (window.Calendly) {
      window.Calendly.initPopupWidget({
        url: url,
        prefill: prefill,
        utm: utm
      })
    } else {
      // Fallback: open in new tab
      window.open(url, '_blank')
    }
  }

  return (
    <Button 
      onClick={openCalendlyPopup}
      className="bg-stone-800 hover:bg-stone-700 text-white"
    >
      Schedule Session
    </Button>
  )
}

// Inline Calendly Widget Component
export function CalendlyInline({ 
  url = process.env.NEXT_PUBLIC_CALENDLY_URL || 'https://calendly.com/revika-healing',
  height = '700px',
  prefill = {},
  utm = {}
}: CalendlyWidgetProps & { height?: string }) {
  
  useEffect(() => {
    if (!window.Calendly) {
      const script = document.createElement('script')
      script.src = 'https://assets.calendly.com/assets/external/widget.js'
      script.async = true
      script.onload = () => {
        if (window.Calendly) {
          window.Calendly.initInlineWidget({
            url: url,
            parentElement: document.getElementById('calendly-inline-widget'),
            prefill: prefill,
            utm: utm
          })
        }
      }
      document.head.appendChild(script)
    } else {
      window.Calendly.initInlineWidget({
        url: url,
        parentElement: document.getElementById('calendly-inline-widget'),
        prefill: prefill,
        utm: utm
      })
    }
  }, [url, prefill, utm])

  return (
    <div 
      id="calendly-inline-widget" 
      style={{ minWidth: '320px', height: height }}
      className="rounded-lg overflow-hidden border border-stone-200"
    />
  )
}

